
import './App.css';
import Home from './views/Home'
import Mainpage from './views/Mainpage';
import Register from './views/Register';
import About from './views/About';
import Login from './views/Login';
import Profile from './components/Profile';
import Logout from './views/Logout'
import Navbar from './components/Navbar';
import Footer from './components/Footer';
import Contact from './views/Contact';
import {BrowserRouter as Router, Routes, Route} from 'react-router-dom'
import { useState } from 'react';
import Dashboard from './components/Dashboard';
import Users from './views/Users';
import Compare from './views/Compare';
import UserContact from './views/UserContact';


function App() {

  const [location, setLocation] = useState('')
  const [loggedIn, setLoggedIn] =useState('false')
  const [admin, setAdmin] =useState('n')


  const token=function(){
  if(sessionStorage.getItem("token")){
    setLoggedIn(sessionStorage.getItem("token")?'true':'false')
    setAdmin(sessionStorage.getItem("admin"))
  }
else{
  setLoggedIn(false)
}
}

return (
    <div>
   <Router>
    <div>
      <Navbar loggedIn={loggedIn} admin={admin}/>
       <Routes>
            <Route path ="/CostOfLiving/" element ={<Home token={token} location={location}/>} />
            <Route path ="/CostOfLiving/Mainpage" element={<Mainpage token={token}/>} />
            <Route path ="/CostOfLiving/Profile" element={<Profile token={token} />} />
            <Route path ="/CostOfLiving/Register" element={<Register />} />
            <Route path ="/CostOfLiving/Login" element={<Login token={token}/>} />
            <Route path ="/CostOfLiving/Logout" element={<Logout token={token}/>} />
            <Route path ="/CostOfLiving/About" element={<About />} />
            <Route path ="/CostOfLiving/Contact" element={<Contact />} />
            <Route path ="/CostOfLiving/Compare" element={<Compare token={token} />} />
            <Route path ="/CostOfLiving/Dashboard" element={<Dashboard token={token} location={location}/>} />
            <Route path ="/CostOfLiving/Users" element={<Users token={token}/>} />
            <Route path ="/CostOfLiving/UserConatct" element={<UserContact token={token} />}/>
           </Routes>
       <Footer loggedIn={loggedIn} admin={admin} />
     </div>
   </Router>
    </div>
  );
}

export default App;
